html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-cart {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-dots {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

* {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

.container {
  width: 1000px;
  margin: 0 auto;
}

@media only screen and (max-width: 1020px) {
  .container {
    margin: 0 20px;
  }
}

.wrap {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.hero {
  height: 100%;
}

.header__logo {
  display: block;
  margin: 30px auto;
}

.header__bottom {
  border-top: 1px solid #eee;
}

.header__bottom .container {
  padding: 15px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header__menu ul li {
  display: inline-block;
}

.header__menu ul li a {
  margin-right: 20px;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  color: #170F11;
}

.header__icons a {
  text-decoration: none;
}

.header__icons .icon {
  fill: #000;
  font-size: 24px;
  margin-left: 20px;
}

.header__icons .cart {
  position: relative;
}

.header__icons .cart-number {
  position: absolute;
  top: -15px;
  right: -12px;
  border-radius: 50%;
  border: 2px solid #fff;
  color: #fff;
  background: #000;
  font-size: 10px;
  padding: 1px 3px;
}

.hero {
  position: relative;
  color: #a9765a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  padding-left: 0;
}

.hero .container {
  margin: 0;
}

.hero__suptitle {
  font-size: 47px;
  margin-bottom: 25px;
  text-transform: uppercase;
  font-weight: normal;
}

.hero__title, .hero__title > span {
  font-size: 77px;
  font-weight: normal;
  line-height: 1;
  text-transform: uppercase;
}

.hero__title {
  margin-bottom: 35px;
}

.hero__title span {
  color: #170f11;
}

.hero__text {
  font-size: 16px;
  line-height: 1.7;
  margin-bottom: 30px;
  max-width: 60%;
}

.btn {
  display: inline-block;
  color: #fff;
  background: #170F11;
  text-decoration: none;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: normal;
  line-height: 1;
  padding: 15px 30px;
}
